            font-semibold\n                                rounded\n                            \"\n                        >\n                            +\n                        </span>\n                    </div>\n\n                    <!-- modal upload -->\n                    <div\n                        class=\"fixed z-10 inset-0 overflow-y-auto\"\n                        v-if=\"add_modal_file_enable\"\n                    >\n                        <div\n                            class=\"\n                                flex\n                                items-end\n                                justify-center\n                                min-h-screen\n                                pt-4\n                                px-4\n                                pb-20\n                                text-center\n                                sm:block sm:p-0\n                            \"\n                        >\n                            <div\n                                class=\"fixed inset-0 transition-opacity\"\n                                aria-hidden=\"true\"\n                            >\n                                <div\n                                    class=\"\n                                        absolute\n                                        inset-0\n                                        bg-gray-500\n                                        opacity-75\n                                    \"\n                                ></div>\n                            </div>\n\n                            <span\n                                class=\"\n                                    hidden\n                                    sm:inline-block sm:align-middle sm:h-screen\n                                \"\n                                aria-hidden=\"true\"\n                                >&#8203;</span\n                            >\n                            <div\n                                class=\"\n                                    inline-block\n                                    align-bottom\n                                    bg-white\n                                    rounded-lg\n                                    text-left\n                                    overflow-hidden\n                                    shadow-xl\n                                    transform\n                                    transition-all\n                                    sm:my-8\n                                    sm:align-middle\n                                    sm:max-w-lg\n                                    sm:w-full\n                                \"\n                                role=\"dialog\"\n                                aria-modal=\"true\"\n                                aria-labelledby=\"modal-headline\"\n                            >\n                                <div\n                                    class=\"\n                                        bg-white\n                                        px-4\n                                        pt-5\n                                        pb-4\n                                        sm:p-6 sm:pb-4\n                                    \"\n                                >\n                                    <div class=\"sm:flex sm:items-start\">\n                                        <div\n                                            class=\"\n                                                mx-auto\n                                                flex-shrink-0 flex\n                                                items-center\n                                                justify-center\n                                                h-12\n                                                w-12\n                                                rounded-full\n                                                bg-red-100\n                                                sm:mx-0 sm:h-10 sm:w-10\n                                            \"\n                                        >\n                                            <!-- Heroicon name: outline/exclamation -->\n                                            <!-- <svg class=\"h-6 w-6 text-red-600\" xmlns=\"http://www.w3.org/2000/svg\" fill=\"none\" viewBox=\"0 0 24 24\" stroke=\"currentColor\" aria-hidden=\"true\">\n                                                <path stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\" d=\"M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z\" />\n                                            </svg> -->\n\n                                            <icon\n                                                :class=\"'h-6 w-6 text-green-600'\"\n                                                :name=\"'folder'\"\n                                            >\n                                            </icon>\n                                        </div>\n                                        <div\n                                            class=\"\n                                                mt-3\n                                                text-center\n                                                sm:mt-0 sm:ml-4 sm:text-left\n                                            \"\n                                        >\n                                            <h3\n                                                class=\"\n                                                    text-lg\n                                                    leading-6\n                                                    font-medium\n                                                    text-gray-900\n                                                \"\n                                                id=\"modal-headline\"\n                                            >\n                                                File Upload\n                                            </h3>\n                                            <div class=\"mt-2\">\n                                                <input\n                                                    type=\"file\"\n                                                    multiple\n                                                    class=\"\n                                                        shadow\n                                                        appearance-none\n                                                        border\n                                                        rounded\n                                                        py-2\n                                                        px-3\n                                                        text-black\n                                                    \"\n                                                    id=\"file\"\n                                                    ref=\"file\"\n                                                    @change=\"handleFileUpload()\"\n                                                />\n                                            </div>\n                                        </div>\n                                    </div>\n                                </div>\n                                <div\n                                    class=\"\n                                        bg-gray-50\n                                        px-4\n                                        py-3\n                                        sm:px-6 sm:flex sm:flex-row-reverse\n                                    \"\n                                >\n                                    <button\n                                        type=\"button\"\n                                        class=\"\n                                            mt-3\n                                            w-full\n                                            inline-flex\n                                            justify-center\n                                            rounded-md\n                                            border border-gray-300\n                                            shadow-sm\n                                            px-4\n                                            py-2\n                                            bg-white\n                                            text-base\n                                            font-medium\n                                            text-gray-700\n                                            hover:bg-gray-50\n                                            focus:outline-none\n                                            focus:ring-2\n                                            focus:ring-offset-2\n                                            focus:ring-indigo-500\n                                            sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm\n                                        \"\n                                        @click=\"addFileModal()\"\n                                    >\n                                        Cancel\n                                    </button>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                    <!-- End add file modal -->\n                </div>\n            </div>\n        </div>\n        <!-- end header -->\n        <!-- categories -->\n        <!-- <div class=\"mt-5 flex flex-row px-5\">\n            <span\n                class=\"px-5 py-1 bg-yellow-500 rounded-2xl text-white text-sm mr-4\"\n                >\n            All items\n            </span>\n            <span class=\"px-5 py-1 rounded-2xl text-sm font-semibold mr-4\">\n            Food\n            </span>\n            <span class=\"px-5 py-1 rounded-2xl text-sm font-semibold mr-4\">\n            Cold Drinks\n            </span>\n            <span class=\"px-5 py-1 rounded-2xl text-sm font-semibold mr-4\">\n            Hot Drinks\n            </span>\n        </div> -->\n        <!-- end categories -->\n        <!-- Media list -->\n        <div class=\"grid grid-cols-3 gap-4 px-5 mt-5 overflow-y-auto h-3/4\">\n            <div\n                v-for=\"(item, index) in file\"\n                :key=\"index\"\n                class=\"\n                    px-3\n                    py-3\n                    flex flex-col\n                    border-4 border-black\n                    dark:border-white\n                    dark:text-white\n                    dark:hover:text-black\n                    dark:hover:border-black\n                    dark:hover:bg-white\n                    border-dashed\n                    rounded-md\n                    h-32\n                    justify-between\n                    hover:bg-black hover:text-white hover:border-white\n                    transition\n                    duration-150\n                \"\n            >\n                <div>\n                    <div class=\"font-bold\">\n                        {{ item.name }}\n                    </div>\n                    <span class=\"font-extrabold text-sm\">\n                        <strong>\n                            {{ item.ext }}\n                        </strong>\n                    </span>\n                </div>\n                <div class=\"flex flex-row justify-between items-center\">\n                    <span class=\"self-end font-bold text-lg\">\n                        {{ item.media_size }}\n                    </span>\n                    <edit-assistant-media\n                        @load_folder=\"loadFiles\"\n                        v-bind:item=\"item\"\n                    >\n                    </edit-assistant-media>\n                    <!-- If is image -->\n                    <div v-if=\"extension.includes(item.ext)\">\n                        <image-edit\n                            @loading=\"loading\"\n                            @load_file=\"loadFiles\"\n                            v-bind:item=\"item\"\n                        >\n                        </image-edit>\n                    </div>\n                    <!-- not editable files -->\n                    <div v-else>\n                        <icon class=\"w-5 h-5\" :name=\"'file'\"> </icon>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <!-- end media list -->\n\n        <!-- Loading  -->\n        <!-- v-if=\"is_loading\" -->\n        <div\n            v-if=\"is_loading\"\n            class=\"\n                fixed\n                top-0\n                left-0\n                right-0\n                bottom-0\n                w-full\n                h-screen\n                z-50\n                overflow-hidden\n                bg-black\n                opacity-75\n                flex flex-col\n                items-center\n                justify-center\n            \"\n        >\n            <div\n                class=\"\n                    loader\n                    ease-linear\n                    rounded-full\n                    border-4 border-t-4 border-gray-200\n                    h-12\n                    w-12\n                    mb-4\n                \"\n            ></div>\n            <h2 class=\"text-center text-white text-xl font-semibold\">\n                Loading...\n            </h2>\n            <p class=\"w-1/3 text-center text-white\">\n                This may take a few seconds, please don't close this page.\n            </p>\n        </div>\n    </div>\n</template>\n<script>\nexport default {\n    name: \"media-content\",\n    props: {\n        parent_id: {\n            type: Number,\n            default: null,\n        },\n        extension: {\n            type: Array,\n            default: [\"jpeg\", \"jpg\", \"png\", \"gif\", \"webp\"],\n        },\n    },\n    data: function () {\n        return {\n            file: [],\n            add_modal_file_enable: false,\n            is_loading: false,\n        };\n    },\n    methods: {\n        showModal() {\n            this.unityToast(\"Drag and drop your file\");\n        },\n        async uploadFile(fileRef) {\n            let formData = new FormData();\n            formData.append(\"file\", fileRef);\n            if (this.parent_id == null) {\n                this.unityToast(\"Select a folder\");\n            } else {\n                await axios\n                    .post(\"/file/upload/\" + this.parent_id, formData, {\n                        headers: {\n                            \"Content-Type\": \"multipart/form-data\",\n                        },\n                    })\n                    .catch((error) => {\n                        if (error.response) {\n                            for (const [key, value] of Object.entries(\n                                error.response.data.errors\n                            )) {\n                                this.unityToast(value);\n                            }\n                        }\n                    });\n            }\n        },\n        async handleFileUpload() {\n            // Reference to the files\n            this.loading();\n            for (const [key, value] of Object.entries(this.$refs.file.files)) {\n                await this.uploadFile(value);\n            }\n            this.loadFiles();\n            this.loading();\n            addFileModal();\n        },\n        async drangAndDropFile(e) {\n            let droppedFiles = e.dataTransfer.files;\n            if (!droppedFiles) return;\n            // this tip, convert FileList to array, credit: https://www.smashingmagazine.com/2018/01/drag-drop-file-uploader-vanilla-js/\n            this.loading();\n            for (const [key, value] of Object.entries(droppedFiles)) {\n                await this.uploadFile(value);\n            }\n            this.loadFiles();\n            this.loading();\n        },\n        async loadFiles() {\n            if (this.parent_id || this.parent_id === 0) {\n                this.loading();\n                await axios\n                    .get(\"/folder/files/\" + this.parent_id, {})\n                    .then((response) => {\n                        this.file = response.data.data;\n                    })\n                    .catch(function (error) {});\n                this.loading();\n            }\n        },\n        loading() {\n            if (this.is_loading) {\n                this.is_loading = false;\n            } else {\n                this.is_loading = true;\n            }\n        },\n        // Modal to that add files\n        addFileModal() {\n            if (this.add_modal_file_enable) {\n                this.add_modal_file_enable = false;\n            } else {\n                this.add_modal_file_enable = true;\n            }\n        },\n    },\n    watch: {\n        // On prop change we load the files lis\n        parent_id: function (val) {\n            if (val === null) {\n                this.file = [];\n            } else {\n                this.loadFiles();\n            }\n        },\n    },\n    created() {},\n    computed: {},\n    mounted() {},\n};\n</script>\n<style>\n.loader {\n    border-top-color: #000;\n    -webkit-animation: spinner 1.5s linear infinite;\n    animation: spinner 1.5s linear infinite;\n}\n\n@-webkit-keyframes spinner {\n    0% {\n        -webkit-transform: rotate(0deg);\n    }\n    100% {\n        -webkit-transform: rotate(360deg);\n    }\n}\n\n@keyframes spinner {\n    0% {\n        transform: rotate(0deg);\n    }\n    100% {\n        transform: rotate(360deg);\n    }\n}\n</style>\n"],"sourceRoot":""}]);
